import Vue from 'vue'
import store from '../../store/index.js' //
import Request from './request.js'
const http = new Request();
http.setConfig((config) => { /*  */
	config.baseUrl = 'https://www.easy-mock.com/mock/5d1443deed2ae904ba2cdd78/mock/'; //
	config.header = {
        'Content-Type': 'application/json;charset=UTF-8'
    };
	return config
})
http.interceptor.request((config, cancel) => { /*  */
	uni.showToast({
		icon:"loading"
	});//
	console.log("",store.getters.getUserData)
	//  
	if (!config.header.isLogin && store.getters.getUserData=="") { 
		uni.reLaunch({
			url: './mine/children/login',
		});
		return false;
	}
	if(config.header.service=="weixin"){
		//
		console.log("");
		config.url = 'https://api.weixin.qq.com/sns/'; //
	}
	/*
	if (!token) { // tokencancel catch() 
		cancel('token ') // catch((err) => {}) err.errMsg === 'token '
	}
	*/
   console.log(config)
	console.log("")
	return config;
})
http.interceptor.response((response) => { /*  */
	uni.hideToast();//
	console.log("")
	return response;
})
export {
	http
};


