//Login
const state = {
    userData: '', //
}
const mutations = {
    set_UserData(state,UserData) {
        //
        state.userData = UserData;
    },

} 


const actions = {
    setUserData ({commit},UserData) {
        //
        commit("set_UserData", UserData);
    },
}
const getters = {
    getUserData(state){
        //
        return state.userData;
    }

}

//
export default {
    state,
    mutations,
    actions,
    getters
}