/**
 * api
 */
import {http} from '../../service/request/index.js' //

/**
 *   
 * @returns {Promise}
 */
export function getisLogin(){
    let datas = {}
	let config = {}
    let e = http.post("login",datas,config);
    return e;
}

/**
 * 
 * @returns {Promise}
 */
export function getLogin(){
    let datas = {}
	let config = {
		header:{
			"isLogin":true //
		}
	}
    let e = http.post("login",datas,config);
    return e;
}

/**
 * 
 * @param appid id
 * @param secret  appSecret
 * @param jscode  code
 * 
 */
export function wexinLogin(appid,secret,jscode){
    let datas = {
		"appid": appid, //id
		"secret": secret, // appSecret
		"js_code": jscode, // code
		"grant_type": "authorization_code", // authorization_code
	}
	let config = {
		header:{
			"isLogin": true, //
			"service": "weixin", //
		}
	}
    let e = http.get("jscode2session",datas,config);
    return e;
}

